import * as React from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { Zipcode } from "../../../recoil/atoms/Zipcode";
import { StreetAddress } from "../../../recoil/atoms/StreetAddress";
import styled from "styled-components";
import Postcode from "../../molecules/postcode/Postcode";
import { useRecoilState } from "recoil";
import axios from "axios";
import {useState, useEffect} from 'react';
import { useHistory } from "react-router";
import { message, Space } from 'antd'
import arrow from '../../../assets/pages/register/arrow.jpg'


const ErrorP = styled.p`
  color: red;
`;

const StyledForm = styled.form`
  max-width: 800px;
  margin: 0 auto;
`;

const StyeldInput = styled.input`
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  border: 1px solid black;
  padding: 10px 10px;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: bold;
  color: black;
`;

const StyledLabel = styled.label`
  line-height: 2;
  text-align: left;
  display: block;
  margin-bottom: 3px;
  margin-top: 10px;
  font-size: 14px;
  font-weight: bold;
  color: black;
`;

const StyledSelect = styled.select`
  width: 200px;
  padding: 0.8em 0.5em;
  border: 1px solid #999;
  font-family: inherit;
  background: url(${arrow}) no-repeat 95% 50%;
  border-radius: 0px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  .select::-ms-expand {
    display: none;
  }
`;

interface FormValues {
  email: string;
  password: string;
  confirmPassword: string;
  username: string;
  nickname: string;
  zipcode: string;
  streetAddress: string;
  detailedAddress: string;
  phone: string;
  gender: number;
  height: number;
  weight: number;
  age: number;
}

const schema = yup.object().shape({
  email: yup.string()
    .required(" ")
    .min(3, "   ")
    .max(50, "   ")
    .matches(/[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i, "   "),
  password: yup.string()
    .required(" ")
    .min(3, " 3  ")
    .max(20, " 20  "),
  confirmPassword: yup.string()
    .oneOf([yup.ref("password"), null], "  ")
    .required(" "),
  username: yup.string()
    .required(" ")
    .min(2, " 2  ")
    .max(10, " 10  ")
    .matches(/[---]/g, " "),
  nickname: yup.string()
    .required(" ")
    .min(2, " 2  ")
    .max(10, " 10  "),
  zipcode: yup.string()
    .required("   "),
  streetAddress: yup.string()
    .required("   "),
  detailedAddress: yup.string()
    .required("  "),
  phone: yup.string()
    .required(" ")
    .max(14, "   "),
  gender: yup.number()
    .required()
    .typeError(" "),
  height: yup.number()
    .required(' ')
    .typeError(" ")
    .min(130, '  ')
    .max(230, '  '),
  weight: yup.number()
    .required(' ')
    .typeError(" ")
    .min(35, '  ')
    .max(200, '  '),
  age: yup.number()
    .required(' ')
    .typeError(" ")
    .min(10, '  ')
    .max(150, '  '),
});

function RegisterStudent() {
  const history = useHistory();
  const [inputPhone, setInputPhone] = useState('');
  const [zipcode, setZipcode] = useRecoilState(Zipcode)
  const [streetAddress, setStreetAddress ] = useRecoilState(StreetAddress)
  
  const { register, setValue, handleSubmit, formState: {errors} } = useForm<FormValues>({
    resolver: yupResolver(schema)
  });

  const onSubmit = (data: FormValues) => {
    let today = new Date();
    let month = today.getMonth()

    let heights: number[] = []
    for (let i = 0; i < 12; i++) {
      if (i < month+1) {
        heights.push(data.height)
      } else {
        heights.push(-1)
      }
    }
    let weights: number[] = []
    for (let i = 0; i < 12; i++) {
      if (i < month+1) {
        weights.push(data.weight)
      } else {
        weights.push(-1)
      }
    }

    axios.post(`${process.env.REACT_APP_API_ROOT_ADDRESS}/api/user`, {
    "email" : data.email,
    "password" : data.password,
    "username" : data.username,
    "nickname" : data.nickname,
    "gender" : data.gender,
    "tel" : data.phone,
    "zipCode" : data.zipcode,
    "street" : data.streetAddress,
    "detailedAddress" : data.detailedAddress,
    "role" : "ROLE_PTSTUDENT",
    "monthlyHeights" : heights,
    "monthlyWeights" : weights,
    "age": data.age
    })
    .then((response) => {
      message.success('  .');
      setZipcode('')
      setStreetAddress('')
      history.push('/');
    })
    .catch((e) => {
      message.success(e.response.data);
    }) 
  }


  const phoneChange = (e) => {
    const regex = /^[0-9\b -]{0,13}$/;
    if (regex.test(e.target.value)) {
      setInputPhone(e.target.value);
    }
  }


  useEffect(() => {
    if (inputPhone.length === 10) {
      setInputPhone(inputPhone.replace(/(\d{3})(\d{3})(\d{4})/, '$1-$2-$3'));
    }
    if (inputPhone.length === 13) {
      setInputPhone(inputPhone.replace(/-/g, '').replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3'));
    }
  }, [inputPhone]);


  return (
    <StyledForm onSubmit={handleSubmit(onSubmit)} >
      <StyledLabel htmlFor="email"></StyledLabel>
      <StyeldInput type="text" placeholder=""{...register("email")} minLength={3} maxLength={50}/>
      {errors.email?.message && <ErrorP>{errors.email?.message}</ErrorP>}

      <StyledLabel htmlFor="password"></StyledLabel>
      <StyeldInput type="password" placeholder="(8~20)" {...register("password")} minLength={3} maxLength={20} />
      {errors.password?.message && <ErrorP>{errors.password?.message}</ErrorP>}

      <StyledLabel htmlFor="confirmPassword"> </StyledLabel>
      <StyeldInput type="password" placeholder=" "{...register("confirmPassword")} minLength={3} maxLength={20}/>
      {errors.confirmPassword?.message && <ErrorP>{errors.confirmPassword?.message}</ErrorP>}

      <StyledLabel htmlFor="username"></StyledLabel>
      <StyeldInput type="text" placeholder=""{...register("username")} minLength={2} maxLength={10}/>
      {errors.username?.message && <ErrorP>{errors.username?.message}</ErrorP>}

      <StyledLabel htmlFor="nickname"></StyledLabel>
      <StyeldInput type="text" placeholder=""{...register("nickname")} minLength={2} maxLength={10} />
      {errors.nickname?.message && <ErrorP>{errors.nickname?.message}</ErrorP>}

      <StyledLabel htmlFor="zipcode"></StyledLabel>
    
        <Space>
      <Postcode />

      <StyeldInput type="text" placeholder=" "{...register("zipcode")} value={zipcode} readOnly />

        </Space>
    
      {errors.zipcode?.message && <ErrorP>{errors.zipcode?.message}</ErrorP>}

      <StyeldInput type="text" placeholder=" "{...register("streetAddress")} value={streetAddress} readOnly />
      {errors.streetAddress?.message && <ErrorP>{errors.streetAddress?.message}</ErrorP>}

      <StyeldInput type="text" placeholder=" "{...register("detailedAddress")} />
      {errors.detailedAddress?.message && <ErrorP>{errors.detailedAddress?.message}</ErrorP>}

      <StyledLabel htmlFor="phone"></StyledLabel>
      <StyeldInput type="text" placeholder="phone"{...register("phone")} maxLength={14}  onChange={phoneChange} value={inputPhone} />
      {errors.phone?.message && <ErrorP>{errors.phone?.message}</ErrorP>}
      
      <StyledLabel htmlFor="gender"></StyledLabel>
      <StyledSelect {...register("gender")} id="gender">
        <option value="">Select</option>
        <option value="0"></option>
        <option value="1"></option>
      </StyledSelect>
      {errors.gender?.message && <ErrorP>{errors.gender?.message}</ErrorP>}

      <StyledLabel htmlFor="age"></StyledLabel>
      <StyeldInput type="number" placeholder=""{...register("age")} min={10} max={150} />
      {errors.age?.message && <ErrorP>{errors.age?.message}</ErrorP>}

      <StyledLabel htmlFor="height"></StyledLabel>
      <StyeldInput type="number" placeholder=""{...register("height")} min={130} max={230} />
      {errors.height?.message && <ErrorP>{errors.height?.message}</ErrorP>}

      <StyledLabel htmlFor="weight"></StyledLabel>
      <StyeldInput type="number" placeholder=""{...register("weight")} min={35} max={200} />
      {errors.weight?.message && <ErrorP>{errors.weight?.message}</ErrorP>}
      
      <StyeldInput 
        type="submit"
        value=""
        onClick={() => {
          setValue('zipcode', `${zipcode}`);
          setValue('streetAddress', `${streetAddress}`);
      }} 
      />
    </StyledForm>
  );
}

export default RegisterStudent;