import React, {useEffect, useState} from 'react'
import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import axios from 'axios';
import {Table, Row, Col, Divider} from 'antd'

const columns = [
  {
    title: ' ',
    dataIndex: 'wei',
    key: 'wei',
  },
  {
    title: '  (PIBW)',
    dataIndex: 'pibw',
    key: 'pibw',
  },
];

const dataSource = [
  {
    key: '1',
    wei: '',
    pibw: '90% ',
  },
  {
    key: '2',
    wei: '',
    pibw: '90%  110% ',
  },
  {
    key: '3',
    wei: '',
    pibw: '110%  120% ',
  },
  {
    key: '4',
    wei: '',
    pibw: ' 120%  130%  /  130%  160%  /  160% ',
  },
];

function PibwChart() {
  const [percentage, setPercentage] = useState(0)
  const [height, setHeight] = useState(0)
  const [weight, setWeight] = useState(0)
  let accessToken = localStorage.getItem('accessToken');

  useEffect(() => {
    let today = new Date()
    let month = today.getMonth()
    axios.get(
      `${process.env.REACT_APP_API_ROOT_ADDRESS}/api/health/myhealth`, {
        headers: {
          "Authorization": `Bearer ${accessToken}`
        }
      }
    )
    .then((response) => {
      setHeight(response.data.data.heightList[month])
      setWeight(response.data.data.weightList[month])
      const kg = response.data.data.weightList[month]
      const cm = response.data.data.heightList[month]
      setPercentage(Math.round(kg/(cm*cm*22/10000)*100))
    })
  }, [])

  return (
    <Row style={{height: "70%", width: "70%", margin: "auto", display: "flex"}}>
    <Col span={12} style={{height: "30%", margin: "auto", marginTop: "1rem"}}>
      <Table columns={columns} dataSource={dataSource} pagination={false} title={() => '(%)=    (%) =  /   100'} footer={() => ` : ${height}  : ${weight}`} />
    </Col>
    <Col span={12} style={{height: "30%"}}>
      <div style={{height: "100%"}}>
        <h1> PIBW {percentage}% .</h1>
        <Divider />
        <div style={{ width: 200, height: 200, margin: 'auto'}}>
          <CircularProgressbar value={percentage} maxValue={200} text={`${percentage}%`} />;
        </div>
        <Divider />
        <p> (PIBW, Percent of Ideal Body Weight):         .
        </p>
      </div>
    </Col>
  </Row>

  )
}

export default PibwChart

